      i = 1;
    } /* Triduum  */
    const officeFinalHora = arrayFinalHora[i];
    return {
      feastNum,
      feastCurrent,
      typeOffice,
      officeCurrent,
      officeAntElement,
      officeTempusLiturgicus1,
